.footer {
  &__container {
    display: flex;
    justify-content: flex-start;
    gap: 16px;

    @media (max-width: 715px) {
      justify-content: center;
      align-items: center;
      text-align: center;
      flex-direction: column;
    }
  }

  &__left {
    width: 35%;

    @media (max-width: 715px) {
      width: 100%;
    }
  }

  &__logo {
  }

  &__social {
    display: flex;
    gap: 10px;

    @media (max-width: 715px) {
      justify-content: center;
    }
  }

  &__nav {
    width: 30%;
    display: flex;
    justify-content: space-between;
    gap: 20px;

    @media (max-width: 715px) {
      width: 100%;
      justify-content: center;
    }
  }

  &__list-first {
  }

  &__list-second {
    max-width: 100px;
  }

  &__item {
    padding-top: 20px;

    &:first-child {
      padding-top: 0;
    }
  }

  &__link {
    margin: 0;

    font-weight: 400;
    font-size: 17px;
    line-height: 27px;
    color: #fff;
  }

  &__right {
  }
}

.footer__list-social {
  display: flex;
  gap: 17px;
  padding-top: 16px;

  @media (max-width: 715px) {
    justify-content: center;
  }
}

.social {
  &__item {
  }

  &__link-footer {
    display: block;
    width: 24px;
    height: 24px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__link-insta {
    background-image: url("../img/insta.svg");
  }

  &__link-twitter {
    background-image: url("../img/twitter.svg");
  }

  &__link-facebook {
    background-image: url("../img/facebook.svg");
  }
}
