.about {
  &__container {
    @include flex-all-sb;

    gap: 60px;

    @include small-tablet {
      gap: 0;
      justify-content: center;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    max-width: 590px;

    @include small-tablet {
      max-width: 100%;
      align-items: center;
      text-align: center;
    }
  }

  &__btn {
    width: 112px;
    height: 112px;
    border-radius: 50%;
    background-color: #118da8;

    .image {
      @include small-tablet {
        width: 50px;
      }
    }

    @include small-tablet {
      width: 100px;
      height: 100px;
    }

    @include mobile {
      width: 70px;
      height: 70px;
    }

  }

  &__link {
    margin: 0;
    padding-top: 20px;

    @include mobile {
      padding-top: 10px;
    }
  }

  &__subtitle {
    margin: 0;
    max-width: 500px;

    font-weight: 400;
    font-size: 65px;
    line-height: 79px;
    letter-spacing: 2px;
    color: #fff;

    @include tablet {
      font-size: 50px;
      line-height: 58px;
    }

    @include small-tablet {
      font-size: 40px;
      line-height: 50px;
    }

    @include mobile {
      font-size: 30px;
      line-height: 38px;
    }
  }

  &__descr {
    margin: 0;
    padding-top: 28px;
    max-width: 555px;

    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: rgba(255, 255, 255, 0.7);
  }

  &__image {
    @include small-tablet {
      display: none;
    }
  }
}
