.order {
  &__container {
  }

  &__content {
    position: relative;
    padding: 60px;
    display: flex;
    justify-content: space-between;
    gap: 72px;

    background-color: #000;
    border-radius: 32px;

    // &::before {
    //   content: "";
    //   position: absolute;
    //   top: 215px;
    //   left: -25px;
    //   transform: translate(-5%, 100%);
    //   width: 147px;
    //   height: 91px;
    //   background: url("../img/order-decor.png") center center / cover no-repeat;
    // }

    @media (max-width: 1135px) {
      padding: 25px;
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 25px;
    }
  }

  &__left {
    width: 50%;

    @media (max-width: 1135px) {
      width: 100%;
    }
  }

  &__span {
    margin: 0;
  }

  &__subtitle {
    margin: 0;

    font-weight: 400;
    font-size: 72px;
    line-height: 79px;
    letter-spacing: 2px;
    color: #fff;

    @include tablet {
      font-size: 40px;
      line-height: 50px;
    }

    @include small-tablet {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__right {
    width: 50%;

    @media (max-width: 1135px) {
      width: 100%;
      max-width: 400px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include mobile {
    gap: 10px;
  }

  &__label {
  }

  &__input {
    width: 100%;
    padding: 25px 20px;
    border: 1px solid #191919;
    border-radius: 6px;
    background-color: #191919;

    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: rgba(255, 255, 255, 0.7);

    @media (max-width: 1135px) {
      padding: 20px 15px;
      font-size: 22px;
    }

    @include mobile {
      padding: 15px 10px;
      font-size: 18px;
    }
  }

  &__btn {
    width: 100%;
    max-width: 100px;
    padding: 10px 15px;

    border-radius: 6px;
    background-color: #118da8;

    font-weight: 700;
    font-size: 15px;
    line-height: 29px;
    color: rgba(255, 255, 255, 0.7);

    @media (max-width: 1135px) {
      margin: 0 auto;
    }
  }
}

.just-validate-error-label {
  padding-top: 2px;
  color: #fff !important;
}
