.header {
  padding: 50px 0;

  @include mobile {
    padding: 25px 0;
  }

  &__container {
    @include flex-all-sb;
  }

  &__logo {
    max-width: 160px;
  }

  &__navbar {
    display: flex;
    align-items: center;
    @include high-tablet {
      position: fixed;
      top: 0;
      left: -100%;
      overflow-y: auto;
      z-index: 100;
      width: 80%;
      max-width: 375px;
      height: 100vh;
      background-color: #191919;
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      box-shadow: 10px 0 23px -5px rgba(34, 60, 80, 0.2);
    }
  }
}

.menu--active {
  left: 0;
}

.navbar {
  &__bar {
  }

  &__list {
    display: flex;
    gap: 40px;

    @include high-tablet {
      padding: 50px;
      display: flex;
      flex-direction: column;
      gap: 30px;
    }
  }

  &__item {
  }

  &__link {
    font-weight: 400;
    font-size: 17px;
    line-height: 27px;
    color: #fff;
    transition: 0.3s;

    @include hover {
      color: #118da8;
    }

    @include high-tablet {
      font-weight: 600;
      font-size: 34px;
      color: #fff;
    }
  }
}
