.hero {
  padding-bottom: 50px;

  @include mobile {
    padding-bottom: 20px;
  }

  &__container {
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 20px;

    @media (max-width: 768px) {
      position: relative;
    }

    @media (max-width: 650px) {
      flex-direction: column;
      gap: 30px;
    }
  }

  &__content {
    height: 100%;
    padding-bottom: 130px;

    @media (max-width: 1040px) {
      padding-bottom: 100px;
    }

    @media (max-width: 992px) {
      padding-bottom: 65px;
    }

    @media (max-width: 735px) {
      padding-bottom: 0px;
    }

    @media (max-width: 650px) {
      text-align: center;
    }
  }

  &__image {
    position: relative;
    width: 100%;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-35%, -32%);
      width: 68px;
      height: 74px;
      background: url("../img/hero-decor.png") center center / cover no-repeat;
    }

    @media (max-width: 650px) {
      order: 2;
    }
  }

  img {
    border-radius: 12px;
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__title {
    margin: 0;
    margin-bottom: 90px;
    font-weight: 400;
    font-size: 70px;
    line-height: 88px;
    letter-spacing: 3px;
    color: #fff;

    @media (max-width: 992px) {
      font-size: 45px;
      line-height: 58px;
    }

    @media (max-width: 680px) {
      font-size: 42px;
      line-height: 50px;
    }

    @media (max-width: 650px) {
      margin-bottom: 20px;

      line-height: 44px;
      letter-spacing: 2px;
    }

    @include mobile {
      margin-bottom: 5px;
    }

    @media (max-width: 425px) {
      font-size: 32px;
      line-height: 38px;
    }
  }

  &__title-underline {
    position: relative;
    display: inline-block;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 11px;
      bottom: 0;
      left: 0;
      background-image: url("../img/underline.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__text-bottom {
    padding-left: 22px;

    @media (max-width: 650px) {
      padding: 0;
    }
  }

  &__span {
    margin: 0;

    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #fff;
  }

  &__social {
    display: flex;
    gap: 17px;
    padding-top: 16px;

    @media (max-width: 650px) {
      align-items: center;
      justify-content: center;
    }

    @include mobile {
      padding-top: 10px;
    }
  }
}

.social {
  &__link {
    display: block;
    width: 32px;
    height: 32px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include mobile {
      width: 25px;
      height: 25px;
    }
  }

  &__link-spotify {
    background-image: url("../img/spotify.svg");
  }

  &__link-oracle {
    background-image: url("../img/oracle.svg");
  }

  &__link-cloud {
    background-image: url("../img/cloud.svg");
  }

  &__link-people {
    background-image: url("../img/people.svg");
  }

  &__link-linke {
    background-image: url("../img/linke.svg");
  }
}
