.articles {
  &__container {
  }

  &__header {
    @include flex-all-sb;
    padding-bottom: 70px;

    @include tablet {
      padding-bottom: 30px;
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 64px;
    line-height: 79px;
    letter-spacing: 2px;
    color: #fff;

    @include tablet {
      font-size: 55px;
    }

    @include small-tablet {
      font-size: 45px;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__link {
    margin: 0;
    margin-top: auto;
    padding: 12px 15px;

    background-color: #118da8;
    border-radius: 6px;

    font-weight: 400;
    font-size: 17px;
    line-height: 27px;
    color: #fff;

    @media (max-width: 580px) {
      padding: 10px 12px;

      font-size: 15px;
      line-height: 20px;
    }
  }

  &__list {
  }

  &__item {
    background-color: #000;
    border-radius: 32px;

    &:not(:last-child) {
      margin-bottom: 60px;
    }

    @include small-tablet {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__card {
    display: flex;
    gap: 55px;
    padding: 40px;

    @include tablet {
      gap: 25px;
    }
  }

  &__img {
    @include small-tablet {
      width: 250px;
    }

    @media (max-width: 600px) {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__subject {
    margin: 0;
    margin-top: 9px;
    padding: 0 19px;

    border-radius: 6px;
    background-color: #191919;

    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: #fff;

    @include small-tablet {
      padding: 0 10px;
      font-size: 15px;
    }
  }

  &__episode {
    margin: 0;
    padding-top: 48px;

    font-weight: 400;
    font-size: 17px;
    line-height: 27px;
    color: #118da8;

    @include tablet {
      padding-top: 15px;
    }
  }

  &__title {
    max-width: 690px;
    margin: 0;
    padding-top: 17px;

    font-weight: 400;
    font-size: 64px;
    line-height: 70px;
    letter-spacing: 2px;
    color: #fff;

    @include tablet {
      font-size: 40px;
      line-height: 50px;
    }

    @include small-tablet {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__info {
    max-width: 760px;
    margin: 0;
    padding: 28px 0 18px;

    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: rgba(255, 255, 255, 0.7);

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 15px;
      line-height: 25px;
    }
  }
}
