.rating {
  &__container {
  }

  &__subtitle {
    margin: 0;
    padding-bottom: 70px;

    text-align: center;
    font-weight: 400;
    font-size: 64px;
    line-height: 79px;
    letter-spacing: 2px;
    color: #fff;

    @include tablet {
      padding-bottom: 30px;
      
      font-size: 55px;
    }

    @include small-tablet {
      font-size: 45px;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__content {
  }

  &__item {
    max-width: 364px;
    height: 100%;

    @media (max-width: 490px) {
      margin: 0 auto;
    }
  }

  &__card {
    max-width: 364px;
    height: 100%;
    padding: 50px;
    display: flex;

    box-sizing: border-box;
    flex-direction: column;

    border-radius: 32px;
    background-color: #000000;

    @media (max-width: 800px) {
      padding: 25px;
    }
  }
}

.swiper-slide {
  width: 360px;
  height: auto;
}

.card {
  &__comment {
    margin: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    flex: 1 0 auto;

    font-weight: 400;
    font-size: 36px;
    line-height: 43px;
    color: #fff;

    @include tablet {
      font-size: 30px;
    }

    @media (max-width: 800px) {
      font-size: 24px;
      line-height: 33px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  &__autor {
    margin: 0;

    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: rgba(255, 255, 255, 0.7);

    @include tablet {
      font-size: 22px;
    }

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 16px;
    }
  }
}
